<script>
  import { GetClassifyAndStyle, UploadImg, Arealist, Ausiness, Brandstore, Storeadd, Storedetails, ModifyStoreInfo } from '@/axios/api'
  import { mapState } from 'vuex'
  export default {
    name: 'store-add-edit', // 普通 添加店铺
    data() {
      return {
        token: sessionStorage.getItem('token'),
        pageTitle: '新增店铺',
        id: '', // 店铺id
        dataInfo: null, // 店铺详情信息
        isStoreNameTips: false,
        isSelectClassifyLayer: false,
        isSelectStyleLayer: false,
        isStoreLogoTips: false,
        isCooperTimeTips: false,
        isSelectBrandLayer: false,
        cooperTime: 1, // 合作期限1： 1-6年；2： 2-12年
        isZiTiActive: 1, // 1 是 2 否
        storeDesc: '',
        storeDescLength: 0,
        addressDesc: '',
        addressDescLength: 0,
        //
        provinceList: [],
        cityList: [],
        areaList: [],
        province: '',
        city: '',
        area: '',
        //
        goodsCircleList: [],
        areaNum: '',
        zNum: '',
        formData: {
          opening_type: '',
          store_name: '',
          cate_id: '',
          style_id: '',
          brand_id: '',
          is_ziqu: '',
          description: '',
          cover: '',
          address: '',
          circle_id: '',
          nickname: '',
          mobile: '',
          telephone: ''
        },
        classifyList: [],
        selectClassifyList: [], // 选择的分类
        styleList: [],
        selectStyleList: [], // 选择的风格
        brandList: [],
        selectBrandList: [], // 选择的品牌
        storeLogo: [], // 店铺logo 取它下面的img_url
        storeZhuImgs: [], // 店铺主图
        storeImgs: [], // 门店环境
        businessImgs: [], // 营业执照&许可证
      }
    },
    computed: {
      ...mapState(["host"])
    },
    components: {},
    mounted() {
      this.$store.state.navActive =  this.$route.query.navActive;

      this.getAreaList('province');
      this.getBusinessList();
      this.getBrandStore();
      this.getClassifyStyle();

      let id = this.$route.query.id;
      if (id) {
        this.id = id;
        this.pageTitle = '编辑店铺';
        setTimeout(() => {
          this.getDataInfo();
        }, 100)
      }
      setTimeout(function(){
        $(".el-upload").css({
          width: '100%',
          height: '100%'
        })
      }, 50);
    },
    methods: {
      getDataInfo() {
        Storedetails({
          token: this.token,
          store_id: this.id
        }).then(res => {
          // console.log(res);
          let dataInfo = res.data;
          this.dataInfo = dataInfo;
          //
          this.cooperTime = dataInfo.opening_type;  // 合作期限
          // 主营分类
          let cate_id = [];
          for(let i in dataInfo.storecatestore) {
            cate_id.push(dataInfo.storecatestore[i].id)
          }
          this.selectClassifyList = dataInfo.storecatestore;
          // 主营风格
          let style_id = [];
          for(let i in dataInfo.stylestore) {
            style_id.push(dataInfo.stylestore[i].id)
          }
          this.selectStyleList = dataInfo.stylestore;
          // 品牌
          let brand_id = [];
          for(let i in dataInfo.brand) {
            brand_id.push(dataInfo.brand[i].id)
          }
          this.selectBrandList = dataInfo.brand;
          //
          this.isZiTiActive = dataInfo.is_ziqu==0 ? 2 : 1;
          this.storeDesc = dataInfo.description;
          this.storeDescLength = dataInfo.description.length;
          //
          this.storeLogo = [{img_url: dataInfo.cover}];
          this.storeZhuImgs = dataInfo.store_img;
          this.storeImgs = dataInfo.store_imgs;
          // 营业执照图
          let b_arr = (dataInfo.business_img).split(",");
          let b_img_url = [];
          for(let i in b_arr) {
            b_img_url.push({
              img_url: b_arr[i]
            })
          }
          this.businessImgs = b_img_url;

          this.formData = {
            opening_type: dataInfo.opening_type,
            store_name: dataInfo.store_name,
            cate_id: cate_id.join(','),
            style_id: style_id.join(','),
            brand_id: brand_id.join(','),
            is_ziqu: dataInfo.is_ziqu==0 ? 2 : 1,
            description: dataInfo.description,
            cover: dataInfo.cover,
            address: dataInfo.address,
            circle_id: dataInfo.circle_name_id,
            nickname: dataInfo.nickname,
            mobile: dataInfo.mobile,
            telephone: dataInfo.telephone
          };
          // let formData = this.formData;
          // 省市区
          this.province = dataInfo.province.id;
          this.getAreaList('city', dataInfo.province.id, () => {
            this.city = dataInfo.city.id;
            this.getAreaList('area', dataInfo.city.id, () => {
              this.area = dataInfo.area.id;
            });
          });
          // formData.province = dataInfo.province;
          // formData.city = dataInfo.city;
          // formData.area = dataInfo.area;
          //
          this.addressDesc = dataInfo.address;
          this.addressDescLength =  dataInfo.address.length;
          if (dataInfo.telephone) {
            this.areaNum = (dataInfo.telephone).split('-')[0];
            this.zNum = (dataInfo.telephone).split('-')[1];
          }

          // 主图
          // let img_uel = [];
          // for(let i in dataInfo.store_img) {
          //   img_uel.push(dataInfo.store_img[i].img_url);
          // }
          // formData.img_uel = img_uel.join(',');
          // 线下门店环境
          // let img_uels = [];
          // for(let i in dataInfo.store_imgs) {
          //   img_uels.push(dataInfo.store_imgs[i].img_url);
          // }
          // formData.img_uels = img_uels.join(',');
          // formData.business_img = dataInfo.business_img;
          // console.log(this.formData)

        })
      },
      // 获取省市区
      getAreaList(types, pid, callback) {
        Arealist({
          pid: pid,
          token: this.token
        }).then(res => {
          // console.log(res);
          switch (types) {
            case 'province':
              this.provinceList = res.data;
              this.cityList = [];
              this.areaList = [];
              this.city = '';
              this.area = '';
              break;
            case 'city':
              this.cityList = res.data;
              this.areaList = [];
              this.city = '';
              this.area = '';
              break;
            case 'area':
              this.areaList = res.data;
              break;
          }
          if(callback){
            callback()
          }
        })
      },
      provinceChange(val) {
        this.getAreaList('city', val)
      },
      cityChange(val) {
        this.getAreaList('area', val)
      },
      areaChange() {},
      // 获取商圈
      getBusinessList() {
        Ausiness({
          token: this.token
        }).then(res => {
          // console.log(res);
          this.goodsCircleList = res.data
        })
      },
      // 获取分类
      getClassifyStyle() {
        GetClassifyAndStyle({
          token: this.token
        }).then(res => {
          // console.log(res);
          let category = res.data.store_category;
          if (category.length > 0) {
            for(let i in category){
              category[i].active = false
            }
          }
          let style = res.data.store_style;
          if (style.length > 0) {
            for(let i in style){
              style[i].active = false
            }
          }
          this.classifyList = category;
          this.styleList = style;
        })
      },
      // 获取品牌
      getBrandStore() {
        Brandstore({
          token: this.token
        }).then(res => {
          let list = res.data;
          if (list.length > 0) {
            for(let i in list) {
              list[i].active = false
            }
          }
          this.brandList = list
        })
      },
      // 选择主营分类
      selectClassify(index) {
        let classifyList = this.classifyList;
        classifyList[index].active = !classifyList[index].active;
        this.classifyList = classifyList
      },
      // 确定（保存）选择的分类
      confirmSelectClassify() {
        let classifyList = this.classifyList;
        let arr = [];
        if (classifyList.length > 0) {
          for(let i in classifyList){
            if(classifyList[i].active){
              arr.push(classifyList[i])
            }
          }
        }
        if (arr.length <= 0) {
          this.$message.error('请选择主营分类');
          return false
        }
        if(arr.length > 10) {
          this.$message.error('最多设置10个');
          return false
        }
        this.isSelectClassifyLayer = false;
        this.selectClassifyList = arr
      },
      // 分类选择layer show
      showClassifyLayer() {
        let classifyList = this.classifyList;
        let selectClassifyList = this.selectClassifyList;
        if(classifyList.length > 0){
          for(let j = 0 ; j < classifyList.length ; j++) {
            if (selectClassifyList.length > 0) {
              for(let i = 0 ; i < selectClassifyList.length ; i++) {
                if(classifyList[j].id === selectClassifyList[i].id) {
                  classifyList[j].active = true;
                  break
                }else{
                  classifyList[j].active = false;
                }
              }
            } else {
              classifyList[j].active = false;
            }
          }
        }
        this.isSelectClassifyLayer = true;
      },
      //========================================
      // 选择主营风格
      selectStyle(index) {
        let styleList = this.styleList;
        styleList[index].active = !styleList[index].active;
        this.styleList = styleList
      },
      // 确定（保存）选择的风格
      confirmSelectStyle() {
        let styleList = this.styleList;
        let arr = [];
        if (styleList.length > 0) {
          for(let i in styleList){
            if(styleList[i].active){
              arr.push(styleList[i])
            }
          }
        }
        if (arr.length <= 0) {
          this.$message.error('请选择主营风格');
          return false
        }
        if(arr.length > 10) {
          this.$message.error('最多设置10个');
          return false
        }
        this.isSelectStyleLayer = false;
        this.selectStyleList = arr
      },
      // 风格选择layer show
      showStyleLayer() {
        let styleList = this.styleList;
        let selectStyleList = this.selectStyleList;
        if(styleList.length > 0){
          for(let j = 0 ; j < styleList.length ; j++) {
            if (selectStyleList.length > 0) {
              for(let i = 0 ; i < selectStyleList.length ; i++) {
                if(styleList[j].id === selectStyleList[i].id) {
                  styleList[j].active = true;
                  break
                }else{
                  styleList[j].active = false;
                }
              }
            } else {
              styleList[j].active = false;
            }
          }
        }
        this.isSelectStyleLayer = true;
      },
      // 品牌
      selectBrand(index) {
        let brandList = this.brandList;
        brandList[index].active = !brandList[index].active;
        this.brandList = brandList
      },
      showBrandLayer() {
        let brandList = this.brandList;
        let selectBrandList = this.selectBrandList;
        if(brandList.length > 0){
          for(let j = 0 ; j < brandList.length ; j++) {
            if (selectBrandList.length > 0) {
              for(let i = 0 ; i < selectBrandList.length ; i++) {
                if(brandList[j].id === selectBrandList[i].id) {
                  brandList[j].active = true;
                  break
                }else{
                  brandList[j].active = false;
                }
              }
            } else {
              brandList[j].active = false;
            }
          }
        }
        this.isSelectBrandLayer = true;
      },
      confirmSelectBrand() {
        let brandList = this.brandList;
        let arr = [];
        if (brandList.length > 0) {
          for(let i in brandList){
            if(brandList[i].active){
              arr.push(brandList[i])
            }
          }
        }
        if (arr.length <= 0) {
          this.$message.error('请选择主营风格');
          return false
        }
        this.isSelectBrandLayer = false;
        this.selectBrandList = arr
      },
      removeSelectBrand(index) {
        this.selectBrandList.splice(index, 1)
      },
      // 添加店铺logo
      handleStoreLogoSuccess(file) {
        const isLt2M = file.size / 1024 / 1024 < 2;
        if (['image/jpeg', 'image/jpg', 'image/png'].indexOf(file.raw.type) <= -1) {
          this.$message.error('请上传正确的图片格式');
          return false
        }
        if (!isLt2M) {
          this.$message.error('上传图片大小不能超过2MB哦!');
          return false
        }
        let param = new FormData();
        param.append('token', this.token);
        param.append('file', file.raw);
        UploadImg(param).then(res => {
          // console.log(res)
          if (res.status === 1) {
            this.storeLogo.push({
              img_url: res.data.src
            })
          } else {
            this.$message.warning(res.msg)
          }
        })
      },
      removeStoreLogo(index) {
        this.storeLogo.splice(index, 1)
      },
      // 店铺主图
      handleStoreZhuImgsSuccess(file, fileList) {
        const isLt2M = file.size / 1024 / 1024 < (600/1024);
        if (['image/jpeg', 'image/jpg', 'image/png'].indexOf(file.raw.type) <= -1) {
          this.$message.error('请上传正确的图片格式');
          return false
        }
        if (!isLt2M) {
          this.$message.error('上传图片大小不能超过600KB哦!');
          return false
        }
        let param = new FormData();
        param.append('token', this.token);
        param.append('file', file.raw);
        UploadImg(param).then(res => {
          // console.log(res)
          if (res.status === 1) {
            if (this.storeZhuImgs.length < 2) {
              this.storeZhuImgs.push({
                img_url: res.data.src
              })
            }
          } else {
            this.$message.warning(res.msg)
          }
        })
      },
      removeStoreZhuImgs(index) {
        this.storeZhuImgs.splice(index, 1);
      },
      // 门店环境图
      handleStoreImgsSuccess(file, fileList) {
        const isLt2M = file.size / 1024 / 1024 < (600/1024);
        if (['image/jpeg', 'image/jpg', 'image/png'].indexOf(file.raw.type) <= -1) {
          this.$message.error('请上传正确的图片格式');
          return false
        }
        if (!isLt2M) {
          this.$message.error('上传图片大小不能超过600KB哦!');
          return false
        }
        let param = new FormData();
        param.append('token', this.token);
        param.append('file', file.raw);
        UploadImg(param).then(res => {
          // console.log(res)
          if (res.status === 1) {
            if (this.storeImgs.length < 5) {
              this.storeImgs.push({
                img_url: res.data.src
              })
            }
          } else {
            this.$message.warning(res.msg)
          }
        })
      },
      removeStoreImgs(index) {
        this.storeImgs.splice(index, 1);
      },
      // 营业执照&许可证
      handleBusinessImgsSuccess(file, fileList) {
        const isLt2M = file.size / 1024 / 1024 < 2;
        if (['image/jpeg', 'image/jpg', 'image/png'].indexOf(file.raw.type) <= -1) {
          this.$message.error('请上传正确的图片格式');
          return false
        }
        if (!isLt2M) {
          this.$message.error('上传图片大小不能超过2MB哦!');
          return false
        }
        let param = new FormData();
        param.append('token', this.token);
        param.append('file', file.raw);
        UploadImg(param).then(res => {
          // console.log(res)
          if (res.status === 1) {
            if (this.businessImgs.length < 5) {
              this.businessImgs.push({
                img_url: res.data.src
              })
            }
          } else {
            this.$message.warning(res.msg)
          }
        })
      },
      removeBusinessImgs(index) {
        this.businessImgs.splice(index, 1);
      },
      gotoBack() {
        this.$router.back(-1)
      },
      // 合作期限
      selectCooperTime(num) {
        this.cooperTime = num
      },
      // 门店自提
      selectIsZiti(num) {
        this.isZiTiActive = num
      },
      // 获取店铺描述的length
      storeDescTextarea() {
        let leng =  this.storeDesc.length;
        if (leng > 150) {
          leng = 150;
          this.storeDesc = (this.storeDesc).substr(0, 150)
        }
        this.storeDescLength = leng
      },
      // 获取地址详情的length
      addressDescTextarea() {
        let leng =  this.addressDesc.length;
        if (leng > 100) {
          leng = 100;
          this.addressDesc = (this.addressDesc).substr(0, 100)
        }
        this.addressDescLength = leng
      },
      onSubmitData() {
        if (!this.id) {
          let status = this.util.power('1', '2'); // 2 add
          if(!status){
            this.$message.error('暂无权限');
          }
        } else {
          let status = this.util.power('1', '3'); // 3 edit
          if(!status){
            this.$message.error('暂无权限');
          }
        }
        let formData = this.formData;
        formData.opening_type = this.cooperTime;
        formData.is_ziqu = this.isZiTiActive;
        if (!formData.store_name) {
          this.$message.error('请输入店铺名称');
          return
        }
        if (formData.store_name.length > 30) {
          this.$message.error('店铺名称长度不超过30字符');
          return
        }
        let selectClassifyList = this.selectClassifyList;
        if(selectClassifyList.length <= 0) {
          this.$message.error('请设置店铺主营分类');
          return
        } else {
          let cate_id = [];
          for(let i in selectClassifyList) {
            cate_id.push(selectClassifyList[i].id)
          }
          formData.cate_id = cate_id.join(",");
        }
        let selectStyleList = this.selectStyleList;
        if(selectStyleList.length <= 0) {
          this.$message.error('请设置店铺主营风格');
          return
        } else {
          let style_id = [];
          for(let i in selectStyleList) {
            style_id.push(selectStyleList[i].id)
          }
          formData.style_id = style_id.join(",");
        }
        let selectBrandList = this.selectBrandList;
        if (selectBrandList.length <= 0) {
          this.$message.error('请选择经营的品牌');
          return
        } else {
          let arr = [];
          for(let i in selectBrandList) {
            arr.push(selectBrandList[i].id)
          }
          formData.brand_id = arr.join(",");
        }
        if(!this.storeDesc) {
          this.$message.error('请输入店铺描述');
          return
        }
        formData.description = this.storeDesc;
        let storeLogo = this.storeLogo;
        if(storeLogo.length <= 0) {
          this.$message.error('请上传店铺LOGO');
          return
        } else {
          let arr = [];
          for(let i in storeLogo) {
            arr.push(storeLogo[i].img_url)
          }
          formData.cover = arr.join(",")
        }
        let storeZhuImgs = this.storeZhuImgs;
        if(storeZhuImgs.length <= 0) {
          this.$message.error('请上传店铺主图');
          return
        } else {
          let arr = [];
          for(let i in storeZhuImgs) {
            arr.push(storeZhuImgs[i].img_url)
          }
          formData.img_uel = arr.join(",")
        }
        let storeImgs = this.storeImgs;
        if(storeImgs.length <= 0) {
          this.$message.error('请上传线下门店环境');
          return
        } else {
          let arr = [];
          for(let i in storeImgs) {
            arr.push(storeImgs[i].img_url)
          }
          formData.img_uels = arr.join(",")
        }
        let businessImgs = this.businessImgs;
        if(businessImgs.length <= 0) {
          this.$message.error('请上传店铺营业执照和许可证');
          return
        } else {
          let arr = [];
          for(let i in businessImgs) {
            arr.push(businessImgs[i].img_url)
          }
          formData.business_img = arr.join(",")
        }
        if(this.province === ''||this.city === '' || this.area === ''){
          this.$message.error('请选择门店所在的省市区');
          return
        }
        formData.province = this.province;
        formData.city = this.city;
        formData.area = this.area;
        if (!this.addressDesc) {
          this.$message.error('请填写门店详细地址');
          return
        }
        formData.address = this.addressDesc;
        if(!formData.nickname) {
          this.$message.error('请填写店铺联系人');
          return
        }
        if(!formData.mobile) {
          this.$message.error('请填写店铺联系人手机号');
          return
        }
        if (this.areaNum&&this.zNum) {
          formData.telephone = this.areaNum + '-' + this.zNum;
        } else {
          formData.telephone = "";
        }
        formData.token = this.token;
        // console.log(formData)
        if (!this.id) {
          Storeadd(formData).then(res => {
            if (res.status == 1) {
              this.$message.success('添加店铺成功');
              this.changePage('/storeIndex', {navActive: '1-0'})
            } else {
              this.$message.error(res.msg)
            }
          });
        } else {
          formData.ids = this.id;
          ModifyStoreInfo(formData).then(res => {
            if (res.status == 1) {
              this.$message.success('编辑店铺成功');
              this.gotoBack();
              // this.changePage('/storeIndex', {navActive: '1-0'})
            } else {
              this.$message.error(res.msg)
            }
          })
        }

      },
    }
  }
</script>
<style lang="less" scoped>
  .el-select-dropdown__item {
    font-size: 14px;
    padding: 0 20px;
    position: relative;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    color: #606266;
    height: 34px;
    line-height: 34px;
    box-sizing: border-box;
    cursor: pointer;
  }
  .el-select-dropdown__item.selected{
    color: #409EFF !important;
  }
</style>



